<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        选择俱乐部
        <template slot="after">
          <button class="btn" @click="handNext">下一步</button>
        </template>
      </topbar>
    </template>
    <div class="club">
      <div class="item hd">
        <span>选择俱乐部</span>
        <span>{{(list[selectedIndex]||{name: '请选择'}).name}}</span>
      </div>
      <template v-for="(row,$index) in list">
        <div class="item" :class="{active:$index===selectedIndex}" @click="handSelect($index)">
          <div>{{row.name}}</div>
          <div><b class="iconfont icon-success"></b></div>
        </div>
      </template>
    </div>
  </fixed-layout>
</template>
<script>
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'

  export default {
    name: '',
    components: {
      Topbar,
      FixedLayout
    },
    props: {},
    data () {
      return {
        selectedIndex: -1,
        list: [
          {
            name: '号角俱乐部1'
          },
          {
            name: '号角俱乐部2'
          },
          {
            name: '号角俱乐部3'
          }
        ]
      }
    },
    watch: {},
    computed: {},
    created () {
      this.init()
    },
    methods: {
      init () {},
      initData () {},
      handSelect (index) {
        this.selectedIndex = index
      },
      handNext () {
        this.$store.commit(this.$actionType.write.set_article_data, {
          club: this.selectedIndex
        })
        this.$router.push({
          path: 'done',
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .club {
    background-color: #fff;
    font-size: 16px;
    width: 100%;
    margin-top: 10px;
    .item {
      display: flex;
      border-bottom: solid #e5e5e5 1px;
      > * {
        padding: 12px 15px 13px;
        flex: 1;
      }
      > :last-child {
        text-align: right;
        color: #3aa7cf;
        visibility: hidden;
      }
      .iconfont {
        font-size: 16px;
      }
      &.active {
        background-color: #3aa7cf;
        color: #fff;
        > :last-child {
          color: #fff;
          visibility: visible;
        }
      }
    }
    .hd {
      color: #888;
      > :last-child {
        visibility: visible;
      }
    }
  }
</style>



